import { getQueriesForElement } from '@testing-library/react'
import { wait } from '@testing-library/user-event/dist/utils'
import React, { useState, useEffect } from 'react'
import personService from './services/persons'

const Person = ({ person, handler }) => (
  <div>
    {person.name} {person.number} <button onClick={(e) => handler(e, person)}>Delete</button>
  </div>
)

const Persons = ({ persons, handler }) => (
  persons.map(person => 
     <Person key={person.name} person={person} handler={handler} />
  )
)


const Filter = ({ value, handler}) => (
  <>
    filter by name 
    <input 
          value={value}
          onChange={handler}
    />
  </>
)

const PersonForm = (props) => (
  <form onSubmit={props.submit}>
  <div>
    name: <input 
            value={props.values[0]}
            onChange={props.handlers[0]}
          />
  </div>
  <div>
    number: <input 
              value={props.values[1]}
              onChange={props.handlers[1]}
            />
  </div>
  <div>
      <button type="submit">add</button>
  </div> 
  </form>  
)

const Notification = ({ message, error }) => {
  if (message === '') return null
  
  const messageStyle = {
    color: 'green',
    background: 'lightgrey',
    fontSize: 20,
    borderStyle: 'solid',
    borderRadius: 5,
    padding: 10,
    marginBottom: 10
  }

  const errorStyle = {
    color: 'red',
    background: 'lightgrey',
    fontSize: 20,
    borderStyle: 'solid',
    borderRadius: 5,
    padding: 10,
    marginBottom: 10
  }

  return (
    <div style={error ? errorStyle : messageStyle} className='noti'>
      {message}
    </div>
  )
}

const App = () => {
  const [persons, setPersons] = useState([]) 
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [newSearch, setNewSearch] = useState('')
  const [newMessage, setNewMessage] = useState('')
  const [error, setError] = useState(false)

  useEffect(() => {
    console.log('effect')
    personService
      .getAll()
      .then(response => {
        console.log('promise fulfilled')
        setPersons(response)
      })
  }, [setTimeout(() => console.log('data polled'), 5000)])

  const addName = (e) => {
    e.preventDefault()
    const noteObj =  {
      name: newName,
      number: newNumber
    }
    if (persons.some(el => el.name === newName)) {
      if (window.confirm(`${newName} is already in the phonebook, replace the old number?`)) {
        const id = persons[persons.findIndex(p => p.name===newName)].id
        personService
          .put(id, noteObj)
          .then(response => {
            console.log(response)
            setNewMessage(`Number for ${newName} has been successfully updated`)
            setTimeout(() => setNewMessage(''), 3000)
          })
      }
    }
    else {
      personService
        .create(noteObj)
        .then(response => {
          console.log(response)
          setNewMessage(`${newName} has been added to the phonebook`)
          setTimeout(() => setNewMessage(''), 3000)
        })  
    }
    setNewName('')
    setNewNumber('')
    console.log(persons)
  }
  
  const deleteName = (e, person) => {
    e.preventDefault()
    if (window.confirm(`Delete ${person.name}?`)) {
      personService
        .remove(person.id)
        .then(response => {
          console.log(response)
        })
        .catch(error => {
          setError(true)
          setNewMessage(`${person.name} has already been deleted`)
          setTimeout(() => {setNewMessage(''); setError(false)}, 3000)
        })
    }
  }

  const handleNoteChange = (e) => {
    console.log(e.target.value)
    setNewName(e.target.value)
  }

  const handleNumberChange = (e) => {
    console.log(e.target.value)
    setNewNumber(e.target.value)
  }

  const handleSearchChange = (e) => {
    console.log(e.target.value)
    setNewSearch(e.target.value)
  }

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={newMessage} error={error}/>
      <Filter value={newSearch} handler={handleSearchChange} />
      <h2>Add New</h2>
      <PersonForm submit={addName} values={[newName, newNumber]} handlers={[handleNoteChange, handleNumberChange]}/>
      <h2>Numbers</h2>
      <Persons persons={persons.filter(p => p.name.includes(newSearch))} handler={deleteName}/>
    </div>
  )
}

export default App